<template>
    <div v-if="ready">
        <div class="card col-12 px0" style='height:auto'>
            <div class="card-header">
                <strong>{{ title }}</strong>
            </div>            
            <div class="card-body card-block">
                <!--entry page-->
                <div v-if="mode=='entry'">
                    <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                        <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                            <div class='ret_wrapper b2' @click="select_mode('main')">                       
                                <div class='inner'>
                                    <div class='flex' style='position: relative;'>
                                        <div style='position: relative;'>
                                            <div class='hexagon b2_first'></div>
                                            <div class='b2_first_char' style='left:1.375rem!important;top:-0.6rem!important'>M</div>
                                        </div>
                                        <div class='hex_subtext'>ain Category </div>
                                        <div class='hex_legen'>Settings</div>
                                    </div>                         
                                </div>
                            </div> 
                        </div>             
                    </div>
                    <div class='vertical_card' style='margin-left:5%;margin-right:5%'>
                        <div style='padding-top:1vh;padding-bottom:1vh;padding-left:1vh;padding-right:1vh'>
                            <div class='ret_wrapper b3' @click="select_mode('sub')">                       
                                <div class='inner'>
                                    <div class='flex' style='position: relative;'>
                                        <div style='position: relative;'>
                                            <div class='hexagon b3_first'></div>
                                            <div class='b3_first_char' style='left:2.75rem!important'>S</div>
                                        </div>
                                        <div class='hex_subtext'>ub Category</div>
                                        <div class='hex_legen'>Settings</div>
                                    </div>                         
                                </div>
                            </div> 
                        </div>             
                    </div>
                </div>
                <!--[END]entry page-->
                <!--Main Category settings-->
                <div v-if="mode=='main'">
                    <label><u class='description'>Main Category List</u></label>
                    <v-data-table
                    v-if="main_ready"
                    :headers="headers"
                    :items="main_cat_list"
                    class="elevation-1"
                    :rows-per-page-items="[5]"
                    >
                        <template v-slot:items="props">
                            <td><a href="" v-on:click.prevent="remove_main_cat(props.item.cat_code)">Remove</a></td>
                            <td>{{ props.item.cat_name }}</td>
                            <td>{{ props.item.cat_code }}</td> 
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>        
                        </template>
                    </v-data-table><br/>
                    <label><u class='description'>Create New Category</u></label>
                    <form class="col-12 px0 flex" @submit.prevent="insert_main">
                        <div class="col-6 pl0">
                            <input type="text" class="form-control" v-model="cat_name" placeholder="Enter Category Name" required/>
                        </div>
                        <div class="col-4">
                            <input type="text" class="form-control" v-model="cat_code" placeholder="Enter Category Code" required/>
                        </div>
                        <div class="col-2 pr0">
                            <button type="submit" class="form-control">Insert</button>
                        </div>
                    </form>
                </div>
                <!--[END]Main Category settings-->
                <!--Sub Category settings-->
                <div v-if="mode=='sub'">
                    <label><u class='description'>Sub Category List</u></label>
                    <div class="col-4 pl0" style='margin-bottom:0.75rem;'>
                        <select class="form-control select" v-model="selected_cat" @change="fetch_sub_category">
                            <option v-for="n in main_cat_list.length" :value="main_cat_list[n-1].cat_code">{{ main_cat_list[n-1].cat_name }}</option>
                        </select>
                    </div>
                    <v-data-table
                    v-if="main_ready"
                    :headers="headers_sub"
                    :items="sub_cat_list"
                    class="elevation-1"
                    :rows-per-page-items="[5]"
                    >
                        <template v-slot:items="props">
                            <td><a href="" v-on:click.prevent="remove_sub_cat(props.item.id)">Remove</a></td>
                            <td>{{ props.item.sub_cat_name }}</td>
                            <td>{{ props.item.sub_category }}</td> 
                        <template v-slot:pageText="props">
                            Items {{ props.pageStart }} - {{ props.pageStop }} out of {{ props.itemsLength }}
                        </template>        
                        </template>
                    </v-data-table><br/>
                    <label><u class='description'>Create New Category</u></label>
                    <form class="col-12 px0 flex" @submit.prevent="insert_sub">
                        <div class="col-6 pl0">
                            <input type="text" class="form-control" v-model="cat_name" placeholder="Enter Category Name" required/>
                        </div>
                        <div class="col-4">
                            <input type="text" class="form-control" v-model="cat_code" placeholder="Enter Category Code" required/>
                        </div>
                        <div class="col-2 pr0">
                            <button type="submit" class="form-control">Insert</button>
                        </div>
                    </form>
                </div>
                <!--[END]Sub Category settings-->              
            </div>
            <div class="card-footer">
                <button type="button" class="btn btn-danger btn-sm" @click="return_prev">
                        <i class="fa fa-undo"></i> Return
                </button>
            </div>
        </div>
    </div>    
</template>
<script>
export default {
    data(){
        return{
            selected_cat:'',
            cat_name:'',
            cat_code:'',
            title : 'Category Settings',
            main_ready:false,
            ready:false,
            mode:'entry',
            main_cat_list:[],
            sub_cat_list:[],
            headers: [
                { text: 'Action',value :'action',sortable: false},
                { text: 'Name',value:'cat_name',sortable: false},
                { text: 'Code',value:'cat_code',sortable: false},
            ], 
            headers_sub: [
                { text: 'Action',value :'id',sortable: false},
                { text: 'Name',value:'sub_cat_name',sortable: false},
                { text: 'Code',value:'sub_category',sortable: false},
            ], 
        }
    },
    async created(){
        this.ready=true;
    },
    async mounted(){
        await this.fetch_main_category();           
        this.main_ready=true;
        
    },
    methods:{
        async remove_main_cat(code){
            var formData = new FormData();
            formData.append('code',code);
            await axios.post('inventory/web/backend/remove_main_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully Deleted Category '+code);
                            this.cat_code = '';
                            this.cat_name = '';
                            this.fetch_main_category();
                            this.$forceUpdate();
                        } else {    
                            this.cat_code = '';
                            this.cat_name = '';
                            this.$alert(response.data);
                        }
                    }
                )
        },
        async insert_main(){
            var formData = new FormData();
            formData.append('name',this.cat_name);
            formData.append('code',this.cat_code);
            await axios.post('inventory/web/backend/insert_main.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.$alert('Successfully Inserted New Record');
                            this.cat_code = '';
                            this.cat_name = '';
                            this.fetch_main_category();
                            this.$forceUpdate();
                        } else {
                            this.cat_code = '';
                            this.cat_name = '';
                            console.log(response);
                        }
                    }
                )
        },
        async insert_sub(){
            var formData = new FormData();
            formData.append('name',this.cat_name);
            formData.append('code',this.cat_code);
            formData.append('main_category',this.selected_cat);
            await axios.post('inventory/web/backend/insert_sub_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data=='OK'){
                            this.cat_code = '';
                            this.cat_name = '';
                            this.fetch_sub_category();
                            this.$forceUpdate();
                        } else {
                            this.$alert($response.data);
                        }
                    }
                )
        },
        async remove_sub_cat(rid){
            var formData = new FormData();
            formData.append('rid',rid);
            await axios.post('inventory/web/backend/remove_sub_cat.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            if(response.data=='OK'){
                                this.$alert('Successfully Deleted Sub-Category');
                                this.fetch_sub_category();
                            } else {
                                this.$alert(response.data);
                            }
                        } else {    
                            this.$alert('Record not found');
                        }
                    }
                )
        },  
        async fetch_main_category(){
            await axios.get('inventory/web/backend/fetch_main_cat.php')
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.main_cat_list = response.data;
                        }
                    }
                )
        },
        async fetch_sub_category(){
            var formData = new FormData();
            formData.append('category',this.selected_cat);
            await axios.post('inventory/web/backend/fetch_sub_category.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.sub_cat_list = response.data;
                        }
                    }
                )
        }, 
        async select_mode(mode){          
            if(mode=='main'){
                this.title = 'Main Category Settings';
                this.mode = mode;
            } else if(mode=='sub'){
                if(this.main_cat_list.length>0){
                    this.selected_cat = this.main_cat_list[0].cat_code;
                    await this.fetch_sub_category();
                    this.title = 'Sub Category Settings';
                    this.mode = mode;
                } else {
                    this.$alert('Please Create Main Category.');
                }               
            }
        },
        return_prev(){
            if(this.mode=='entry'){
                this.$router.go(-1);
            } else {
                this.mode='entry';
                this.title = 'Category Settings';
            }
        }
    }
}
</script>
<style scoped>
.px0{
    padding-left:0;
    padding-right:0;
}
.pl0{
    padding-left:0;
}
.pr0{
    padding-right:0;
}
.description{
    font-weight: 600;
}
</style>


